import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManageRolesComponent } from './manage-roles/manage-roles.component';
import { EditRoleComponent } from './pages/edit-role/edit-role.component';
import { NewRoleComponent } from './pages/new-role/new-role.component';
import { DeleteRoleComponent } from './pages/delete-role/delete-role.component';
import { ManageRolesPageComponent } from './pages/manage-roles-page/manage-roles-page.component';

const routes: Routes = [
  {
    path: '',
    component: ManageRolesComponent,
    data: { animationState: 'One' },
    children: [
      {
        path: '',
        component: ManageRolesPageComponent,
        data: { animationState: 'One', pageTitle: 'Manage Roles' },
      },
      {
        path: 'edit-role',
        component: EditRoleComponent,
        data: { animationState: 'Two', pageTitle: 'Edit Role' },
      },
      {
        path: 'new-role',
        component: NewRoleComponent,
        data: { animationState: 'Two', pageTitle: 'New Role' },
      },
      {
        path: 'delete-role',
        component: DeleteRoleComponent,
        data: { animationState: 'Two', pageTitle: 'Delete Role' },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ManageRolesRoutingModule {}
