import { AfterViewChecked, Component, OnInit } from '@angular/core';
import { RouterOutlet } from '@angular/router';
import { fadeAnimation, openCloseAnimation } from 'src/app/fade-in-transition';
import { routeTransitionsAnimations } from '../../route-transition-animations';

@Component({
  selector: 'app-manage-roles',
  templateUrl: './manage-roles.component.html',
  styleUrls: ['./manage-roles.component.scss'],
  animations: [openCloseAnimation],
  // animations: [fadeAnimation],
})
export class ManageRolesComponent implements OnInit, AfterViewChecked {
  constructor() {}

  ngOnInit(): void {}

  ngAfterViewChecked(): void {}

  prepareRoute(outlet: RouterOutlet) {
    return (
      outlet &&
      outlet.activatedRouteData &&
      outlet.activatedRouteData[`animationState`]
    );
  }
}
